import React from 'react';

export default class ReviewPageComponent extends React.Component{
  render(){
    return (
      <div style={{width: "718px",}} id="responseModal">
        <div className="modal-header">
          <div className="caption">
            <img src="static/images/check%20mark.png" />
              提交成功
          </div>
        </div>
        <div className="modal-body">
          <p>温馨提示:系统已将您的订单请求发送给律师,律师会在48小时内给您答复。请耐心等待。</p>
          <div className="order-info-block">
            <p>服务提供商:{this.props.sellerName}</p>
          </div>
          <p>如需要帮助,请联系客服人员: <span style={{color: "#fc7777"}}>(800) 685-6947</span></p>
        </div>
      </div>
    );
  }
}


